___CSS_LOADER_EXPORT___.push([module.id, `.featuredproducts-container {
  padding: 2rem;
  background: #f9f9f9;
  min-height: 100vh;
  text-align: center;
}

.featuredproducts-title {
  font-size: 2rem;
  margin-bottom: 2rem;
  color: #333;
}

.products-list {
  display: grid;
  grid-template-columns: repeat(2, 1fr); /* 2 items per row */
  gap: 1.5rem;
  justify-content: center;
}

.product-tile {
  display: grid;
  grid-template-columns: 1fr 1fr; /* Two equal sections: image and details */
  aspect-ratio: 2 / 1; /* Ensure width is twice the height */
  background: white;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.product-tile:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 10px rgba(0, 0, 0, 0.2);
}

.product-image-container {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f4f4f4;
  overflow: hidden;
}

.product-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.empty-picture {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: #999;
  font-size: 1rem;
}

.product-details {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: left;
}

.product-name {
  font-size: 1.4rem;
  font-weight: bold;
  color: #007bff;
  margin-bottom: 0.5rem;
}

.product-price {
  font-size: 1.2rem;
  color: #28a745;
  margin-bottom: 0.5rem;
}

.product-stock {
  font-size: 1rem;
  color: #6c757d;
  margin-bottom: 0.5rem;
}

.product-description {
  font-size: 0.9rem;
  color: #555;
  line-height: 1.4;
}
